body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #937FE1;
  min-height: 100vh; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #E7E2FE;
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: TodaySHOP-Medium;
  font-size: 16px;
  color: #521DA9;
  letter-spacing: 0.44px;
  line-height: 24px; }
  .App-header a {
    margin: 15px 0px 10px 20px;
    text-decoration: none; }
  .App-header .cross {
    font-size: 30px;
    font-weight: lighter;
    margin-right: 20px;
    float: right; }

.body h1 {
  color: white; }

.body .wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  .body .wrapper h1 {
    color: white; }
  .body .wrapper .wrapper-cupones {
    display: grid;
    grid-template-columns: repeat(2, minmax(380px, 1fr)); }
    .body .wrapper .wrapper-cupones .cupon {
      background-color: white;
      width: 350px;
      height: 210px;
      margin: auto;
      margin-left: 17.5px;
      margin-right: 17.5px;
      margin-bottom: 40px;
      border-radius: 5px;
      display: grid;
      grid-template-columns: 3fr 1fr; }
      .body .wrapper .wrapper-cupones .cupon .right-cupon {
        background: #E9F2DF;
        border-radius: 0px 5.19px 5.19px 0px;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center; }
        .body .wrapper .wrapper-cupones .cupon .right-cupon svg {
          margin: auto; }
      .body .wrapper .wrapper-cupones .cupon .left-cupon h3 {
        padding-left: 5px;
        padding-right: 5px;
        font-size: 25px; }

.body .wrapper-blocked {
  display: grid;
