      margin-right: 17.5px;
      margin-bottom: 40px;
      border-radius: 5px;
      display: grid;
      grid-template-columns: 3fr 1fr; }
    .body .wrapper-blocked .blocked .cupon {
      background-color: white;
      width: 350px;
      height: 210px;
      margin: auto;
      margin-left: 17.5px;
      margin-right: 17.5px;
      margin-bottom: 40px;
      border-radius: 5px;
      display: grid;
      grid-template-columns: 3fr 1fr; }
      .body .wrapper-blocked .blocked .cupon .left-cupon {
        color: black;
