      .body .wrapper-blocked .blocked .cupon .right-cupon {
        opacity: 0.56;
        background: #7A64CC;
        border-radius: 0px 5.19px 5.19px 0px;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center; }
        .body .wrapper-blocked .blocked .cupon .right-cupon svg {
          margin: auto; }

.rosa {
  background-image: -webkit-linear-gradient(top, #FF6BCA 0%, #FF78CF 42%, #FF00A8 100%);
  background-image: linear-gradient(-180deg, #FF6BCA 0%, #FF78CF 42%, #FF00A8 100%);
  border-radius: 5px;
  padding: 10px 45px 10px 45px;
  font-size: 18px;
  color: white;
  font-weight: bold;
  margin-top: 20px; }

.green {
  background-image: -webkit-linear-gradient(top, #86BA2B 0%, #2A740D 100%);
  background-image: linear-gradient(-180deg, #86BA2B 0%, #2A740D 100%);
  border-radius: 5px;
  padding: 5px 25px 5px 25px;
  font-size: 18px;
  color: white;
  font-weight: bold;
  margin-top: 0px; }

@-webkit-keyframes App-logo-spin {
